* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    transition: background .5s;
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
}

#quote-box {
    background: #fff;
    border-radius: 3px;
    width: 50vh;
    padding: 40px 50px;
}

.quote-text {
    height: auto;
    clear: both;
    margin-bottom: 2rem;
    display: inline;
}


.quote-text i {
    font-size: 1em;
    margin-right: 0.4em;
}

.quote-author {
    text-align: end;
    margin-top: .5rem;
    margin-bottom: 2rem;
    font-size: large;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

.button {
    border: none !important;
    box-shadow: none !important;
}